import React from "react"
import { Box, Button } from 'grommet';
import styled from 'styled-components'
import Square from '../Square'
import QiMen from '../Square/QiMen'

const BoxWrapper = styled(Box)`
  width: 32%;
  margin: 2px;
`

const Home = ({ chart }) => {
  return <>
  <Box 
    background="brand"
    alignSelf="center"
    justify="center"
    direction="row"
    wrap={true}
    fill="horizontal"
  >

  {
    chart && chart.chart && chart.chart.map((e, i) => <BoxWrapper
      border={true}
    >
    <Square data={{
        structure: chart.structure,
        ...e,
        index: i
      }} Template={QiMen} />
    </BoxWrapper>)
  }
  </Box>
  </>
}

export default Home
